@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);

:root{
  /* --primary-color: #cd0982; */
  --primary-color: #EE4D2D;
  --second-color: #EE4D2D;
  --text-color:#333;
  --text-label:#858585;
  --white-color:#fff;
  --black-color:#000;
  --border-color:#dbdbdb;
  --star-gold-color:#FFCE3E;
  --border-radius: 2px;
  --header-height: 120px;
  --navbar-height: 34px;
  --header-with-search-height: calc(var(--header-height) - var(--navbar-height))
}

*{
  box-sizing: inherit;
  /* them */
  margin: 0;
  padding: 0;
}

html{
  font-size: 62.5%;
  line-height: 1;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

/* Responsive */

.grid{
  /* width: 1200px; */
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  overflow-x: hidden;
}

.grid__full-width{
  width: 100%;
}

.grid__row{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
  /* them */
  padding: 0 50px;
}

.grid__row--reset{
  padding: 0;
}

.grid__column-2{
  padding-left: 5px;
  padding-right: 5px;
  width: 16.6667%;
}

.grid__column-2-4{
  padding-left: 5px;
  padding-right: 5px;
  width: 20%;
}

.grid__column-10{
  padding-left: 5px;
  padding-right: 5px;
  width: 83.3334%;
}

/* Animation */

@-webkit-keyframes fadeIn{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@keyframes fadeIn{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

@-webkit-keyframes growth {
  from{
    -webkit-transform: scale(var(--growth-from));
            transform: scale(var(--growth-from));
  }
  to{
    -webkit-transform: scale(var(--growth-to));
            transform: scale(var(--growth-to));
  }
}

@keyframes growth {
  from{
    -webkit-transform: scale(var(--growth-from));
            transform: scale(var(--growth-from));
  }
  to{
    -webkit-transform: scale(var(--growth-to));
            transform: scale(var(--growth-to));
  }
}



@-webkit-keyframes NotifyGrowth{
  from{
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}



@keyframes NotifyGrowth{
  from{
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  to{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

/* Modal */


.modal{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: -webkit-flex;
  display: flex;
  -webkit-animation: fadeIn linear .1s;
          animation: fadeIn linear .1s;
  display: none;
}

.modal--show{
  display: block;
}

.modal__overlay{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
}

.modal__body{
  width: 100%;
  height: 100%;
  --growth-from: .7;
  --growth-to: 1;
  margin: auto;
  position: relative;
  z-index: 2;
  -webkit-animation: growth linear .1s;
          animation: growth linear .1s;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}


/* button primary */

.btn{
  background-color: #fff;
  background-color: var(--white-color);
  color: #333;
  color: var(--text-color);
  min-width: 124px;
  height: 34px;
  text-decoration: none;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  font-size: 1.5rem;
  padding: 0 12px;
  outline: none;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.btn.btn--normal:hover{
  background-color: rgba(0, 0, 0, .05);
}

.btn--size-s{
  height: 32px;
  font-size: 1.2rem;
  padding: 0 8px;
}

.btn.btn--primary{
  background-color: #EE4D2D;
  background-color: var(--primary-color);
  color: #fff;
  color: var(--white-color);
}


.btn.btn--second{
  border: 1px solid #EE4D2D;
  border: 1px solid var(--primary-color);
  background-color: #ffeee8;
  color: #EE4D2D;
  color: var(--primary-color);
}

.btn.btn--second:hover{
  background-color: #f8efec;
}

.btn.btn--disabled{
  cursor: default;
  opacity: .5;
}


/* selection */
.select-input{
  position: relative;
  padding: 0 12px;
  border-radius: 2px;
  min-width: 200px;
  height: 34px;
  background-color: #fff;
  background-color: var(--white-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.select-input__label{
  font-size: 1.4rem;
}

.select-input-icon{
  font-size: 1.4rem;
  color: #4A4A4A;
}

.select-input:hover .select-input__list{
  display: block;
}

.select-input__list{
  display: none;
  position: absolute;
  z-index: 1;
  width: 100%;
  top: 36px;
  right: 0;
  background-color: #fff;
  background-color: var(--white-color);
  border-radius: 2px;
  padding: 2px 0;
  list-style: none;
  -webkit-animation: fadeIn linear .2s;
          animation: fadeIn linear .2s;
}

.select-input__list::before{
  position: absolute;
  content: "";
  top: -10px;
  right: 0;
  width: 100%;
  height: 10px;
}

.select-input__item{
  padding: 8px 12px;
}

.select-input__item-btn{
  background: none;
  border: none;
  text-decoration: none;
  font-size: 1.4rem;
  color: #333;
  color: var(--text-color);
  cursor: pointer;
}

.select-input__item-btn:hover{
  color: #EE4D2D;
  color: var(--primary-color);
}


/* .select-input__item--active .select-input__item-btn{
  color: var(--primary-color);
} */

.select-input__item-icon{
  font-size: 1.2rem;
  float: right;
  display: none;
}

.select-input__item--active .select-input__item-icon{
  display: inline-block;
  color: #EE4D2D;
  color: var(--primary-color);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.header{
  height: var(--header-height);
  /* background-image: linear-gradient(0, #fe6433,#f53e2d); */
  background-color: var(--primary-color);
}

.header-wrap{
  margin: 0 50px;
}

.header__navbar{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: var(--navbar-height);
}

.header__navbar-list{
  list-style: none;
  padding-left: 0;
  margin: 4px 0 0 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.header__navbar-item{
  padding: 0 8px;
  position: relative;
}

.header__navbar-item,
.header__navbar-item-link{
  display: block;
  font-size: 1.5rem;
  color: var(--white-color);
  text-decoration: none;
  font-weight: 200;
}

.header__navbar-item--showQR:hover .header__qr{
  display: block;
} 

.header__navbar-item--showNotify:hover .header__notify{
  display: block;
}


.header__navbar-item:hover,
.header__navbar-item-social:hover,
.header__navbar-item-link:hover{
  cursor: pointer;
  color: rgba(255, 255, 255, .7);
}

.header__navbar-item-social:nth-child(2){
  margin-left: 3px;
}

.header__navbar-item--bold{
  font-weight: 400;
}

.header__navbar-item--separation::after{
  padding: 0;
  content:"";
  display: block;
  position: absolute;
  border: 1px solid var(--white-color);
  height: 1.5rem;
  top: 50%;
  right: -2px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.header__navbar-item-social{
  color:var(--white-color);
  text-decoration: none;
}

.header__navbar-icon{
  font-size: 1.8rem;
  margin: 0 4px;
}

.header__navbar-item_span{
  cursor: text;
  color: var(--white-color);
  position: relative;
  top: -2px;
}

/* login success */

.header__navbar-user{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  top: 6px;
}

.header__navbar-user-avatar{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  object-fit: cover;
  margin: auto;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.header__navbar-user-name{
  margin-top: 6px;
  margin-left: 6px;
  font-size: 1.5rem;
  font-weight: 400;
}

.header__navbar-user:hover .header__navbar-user-menu{
  display: block;
}

.header__navbar-user-menu{
  position: absolute;
  z-index: 1;
  list-style: none;
  top: calc(100% + 8px);
  right: 8px;
  border-radius: var(--border-radius);
  background-color: var(--white-color);
  width: 160px;
  padding-left: 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, .3);
  display: none;
  -webkit-animation: NotifyGrowth ease-in .2s;
          animation: NotifyGrowth ease-in .2s;
  -webkit-transform-origin: calc(100% - 10px) top;
          transform-origin: calc(100% - 10px) top;
  will-change: opacity,transform;
}

.header__navbar-user-menu::before{
  content: "";
  position: absolute;
  top: -24px;
  right: 16px;
  border-width: 12px 15px;
  border-style: solid;
  border-color: transparent transparent var(--white-color) transparent;
}

.header__navbar-user-item{
  padding: 12px;
}

.header__navbar-user-item:hover{
  background-color: #F2F2F2;
}

.header__navbar-user-item a{
  text-decoration: none;
  font-size: 1.4rem;
  font-weight: 400;
  color: var(--black-color);
  z-index: 10;
}

.header__navbar-user-item:first-child{
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.header__navbar-user-item:last-child{
  border-top: 1px solid #F2F2F2;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.header__navbar-user-item:last-child button{
  font-size: 1.4rem;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  color: var(--primary-color);
}

/* Header QR code */
.header__qr{
  width: 186px;
  background-color: var(--white-color);
  position: absolute;
  z-index: 1;
  top: calc(100% + 8px);
  left: 8px;
  padding: 2px;
  border-radius: var(--border-radius);
  display: none;
  -webkit-animation: fadeIn ease .3s;
          animation: fadeIn ease .3s;
  box-shadow: 0 10px 10px rgba(0, 0, 0, .1);
}

.header__qr::before{
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 15px;
  top: -10px;
  left: 0;
}

.header__qr-img{
  display: block;
  width: 182px;
}

.header__qr-apps{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header__qr-link:first-child{
  text-decoration: none;
  margin: 0 0 10px 10px;
}

.header__qr-link:last-child{
  text-decoration: none;
  margin: 0 10px 10px 0;
}

.header__qr-download-img{
  height: 16px;
}


.header__notify{
  position: absolute;
  top: calc(100% + 10px);
  right: calc(0% + 8px);
  width: 404px;
  background-color:var(--white-color);
  border: 1px solid #d3d3d3;
  border-radius: var(--border-radius);
  cursor: default;
  -webkit-animation: NotifyGrowth ease-in .2s;
          animation: NotifyGrowth ease-in .2s;
  -webkit-transform-origin: calc(100% - 32px) top;
          transform-origin: calc(100% - 32px) top;
  will-change: opacity,transform;
  display: none;
  z-index: 2;
}

.header__notify::before{
  content: "";
  display: block;
  position: absolute;
  border-width: 20px 27px;
  border-style: solid;
  border-color: transparent transparent var(--white-color) transparent;
  top: -30px;
  cursor: pointer;
  right: calc(0% + 5px);
}

.header__notify::after{
  position: absolute;
  content: "";
  top: -18px;
  right: 0;
  height: 20px;
  width: 25%;
  cursor: pointer;
}

.header__notify-header{
  height: 40px;
}

.header__notify-header h3{
  color: #d3d3d3;
  margin: 0;
  font-weight: 400;
  font-size: 1.5rem;
  text-transform: capitalize;
  line-height: 40px;
  margin-left: 12px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.header__notify-list{
  padding-left: 0;
  height: 55vh;
  overflow-y: auto;
}

.header__notify-list::-webkit-scrollbar {
  width: 0;
}

.header__notify-item--viewed{
  background-color: rgba(238, 75, 43, 0.08);
}

.header__notify-item:hover{
  background-color: #f7f7f7;
}

.header__notify-link{
  display: -webkit-flex;
  display: flex;
  padding: 12px;
  text-decoration: none;
}

.header__notify-img{
  width: 60px;
  object-fit: contain;
}

.header__notify-info{
  margin-left: 8px;
}

.header__notify-name{
  display: block;
  font-size: 1.4rem;
  color: var(--text-color);
  font-weight: 400;
  line-height: 1.6rem;
}

.header__notify-des{
  display: block;
  font-size: 1.2rem;
  color: #576F6E;
  line-height: 1.5rem;
}

.header__notify-footer{
  height: 30px;
  background-color: var(--white-color);
  text-align: center;
}

.header__notify-footer-link{
  text-decoration: none;
  color: var(--text-color);
  text-align: center;
  line-height: 30px;
  font-size: 1.4rem;
  font-weight: 400;
}

.header__notify-footer-link:hover{
  text-decoration: underline;
}

/* header with search css */

.header-with-search{
  height: var(--header-with-search-height);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 8px;
}

.header__logo{
  width: 200px;
}

.header__logo-link{
  text-decoration: none;
}

.header__logo-img{
  width: 110px;
  height: 70px;
  object-fit: cover;
  margin-left: 15px;
}

.header__search{
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 40px;
  border-radius: var(--border-radius);
  background-color: var(--white-color);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header__cart{
  width: 150px;
  text-align: center;
}

.header__cart-wrap{
  position: relative;
  display: inline-block;
  padding: 0 12px;
  cursor: pointer;
}

.header__cart-wrap:hover .header__cart-list{
  display: block;
}

.header__cart-icon{
  color: var(--white-color);
  font-size: 2.5rem;
}

/* cart length */
.header__cart-length{
  position: absolute;
  padding: 1px 6px;
  background-color: var(--white-color);
  color: var(--primary-color);
  border: 1.5px solid  var(--primary-color);
  border-radius: 10px;
  font-size: 13px;
  font-weight: 600;
  top: -8px;
  right: 0px;
}

/* cart list */

.header__cart-list{
  position: absolute;
  z-index: 1;
  top: calc(100% + 10px);
  right: 1px;
  width: 410px;
  background-color: var(--white-color);
  box-shadow: 0px 3px 10px #ccc;
  border-radius: var(--border-radius);
  -webkit-animation: NotifyGrowth ease-in .2s;
          animation: NotifyGrowth ease-in .2s;
  -webkit-transform-origin: calc(100% - 20px) top;
          transform-origin: calc(100% - 20px) top;
  will-change: opacity,transform;
  cursor: default;
  display: none;
}

.header__cart-list::before{
  position: absolute;
  content: "";
  top: -20px;
  right: 0;
  width: 25%;
  height: 25px;
}

.header__cart-list::after{
  position: absolute;
  content: "";
  border-width: 18px 22px;
  border-style: solid;
  border-color: transparent transparent var(--white-color) transparent;
  top: -29px;
  right: 0px;
  cursor: pointer;
}

.header__cart-list--no-cart{
  padding: 24px 0 10px 0;
}

.header__cart-list--no-cart .header__cart-no-cart-img,
.header__cart-list--no-cart .header__cart-under-title{
  margin-right: auto;
  margin-left: auto;
  display: block;
}


.header__cart-no-cart-img{
  display: none;
  width: 60%;
}

.header__cart-under-title{
  display: none;
  width: 100%;
  font-size: 15px;
  font-weight: 400;
  color: var(--text-color);
  text-transform: capitalize;
  margin-top: 15px;
}


.header__search-input-wrap{
  -webkit-flex: 1 1;
          flex: 1 1;
  height: 100%;
  position: relative;
}

.header__search-input{
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 1.5rem;
  color: var(--text-color);
  padding: 0 16px;
  border-radius: var(--border-radius);
}

.header__search-select{
  border-left: 1px solid #ccc;
  padding-left: 16px;
  cursor: pointer;
  position: relative;
}

.header__search-select:hover .header__search-option{
  display: block;
}

.header__search-option{
  display: none;
  position: absolute;
  top: calc(100% + 14px);
  right: -2px;
  width: 120px;
  list-style: none;
  box-shadow: 0 1px 2px rgba(0, 0, 0, .1);
  padding-left: 0;
  border-radius: var(--border-radius);
  -webkit-animation: fadeIn linear .2s;
          animation: fadeIn linear .2s;
}

.header__search-option::before{
  content: "";
  display: block;
  width: 100%;
  height: 20px;
  position: absolute;
  top: -16px;
  left: 0;
}

.header__search-option-item{
  background-color: var(--white-color);
  padding: 8px 0px 8px 15px;
}

.header__search-option-item:first-child{
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}

.header__search-option-item:last-child {
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.header__search-option-item:hover{
  background-color: #fafafa;
}

.header__search-option-item span{
  font-size: 1.4rem;
  color: var(--text-color);
}

.header__search-option-item i{
  font-size: 1.2rem;
  color: var(--primary-color);
  margin-left: 8px;
  display: none;
}

.header__search-option-item--active i{
  display: inline-block;
}

.header__search-select-label{
  font-size: 1.4rem;
  color: var(--text-color);
}

.header__search-select-icon{
  font-size: 1.4rem;
  color: #4A4A4A;
  margin: 0 2px 0 8px;
  position: relative;
  top: 1px;
}

.header__search-btn{
  background-color: var(--primary-color);
  border: none;
  height: 34px;
  width: 60px;
  margin: 0 3px;
  border-radius: 2px 0 0 3px;
  cursor: pointer;
}

.header__search-btn:hover{
  background-color: #f7684b;
}

.header__search-btn-icon{
  font-size: 1.6rem;
  color: var(--white-color);
}

.header__search-input:focus ~ .header__search-history{
  display: block;
}

/* search history */

.header__search-history{
  display: none;
  position: absolute;
  z-index: 1;
  top: calc(100% + 3px);
  left: 0;
  width: calc(100% - 16px);
  background-color: var(--white-color);
  border-radius: var(--border-radius);
  box-shadow: 0 1px 3px #ccc;
}

