.auth-form__btn-close{
  position: absolute;
  top: 5px;
  right: 5px;
  background: none;
  border: none;
}

.auth-form__btn-close i{
  font-size: 2.3rem;
  background-color: var(--white-color);
  color: var(--black-color);
  cursor: pointer;
}

.auth-form__signIn--show{
  display: block;
}

.auth-form__signUp--show{
  display: block;
}

.auth-form__container{
  padding: 25px 32px 0 32px;
}

.auth-form__header{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0 1.2rem;
  margin-top: 10px;
}

.auth-form__heading{
  font-size: 2.2rem;
  font-weight: 400;
  color: var(--text-color);
}

.auth-form__switch-btn{
  font-size: 1.6rem;
  color: var(--primary-color);
  font-weight: 400;
  cursor: pointer;
}

/* .auth-form__form{
  
} */

.auth-form__input{
  margin-top: 16px;
  width: 100%;
  height: 40px;
  padding: 0 12px;
  font-size: 1.4rem;
  border-radius: var(--border-radius);
  border: 1px solid var(--border-color);
  outline: none;
}

.auth-form__input:focus{
  border: 1px solid #777;
}

.auth-form__aside{
  margin-top: 18px;
}

.auth-form__policy-text{
  font-size: 1.2rem;
  line-height: 1.8rem;
  text-align: center;
  /* padding: 0 12px; */
}

.auth-form__help{
  font-size: 1.4rem;
}

.auth-form__text-link{
  color: var(--primary-color);
  text-decoration: none;
}

.auth-form__controls{
  margin-top: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.auth-form__controls-back{
  margin-right: 8px;
}

.auth-form__socials{
  background-color: #f5f5f5;
  padding: 14px 18px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 22px;
}

.auth-form__socials-icon{
  font-size: 2rem;
  position: relative;
  top: 3px;
}

.auth-form__socials--facebook{
  color: var(--white-color);
  background-color: #3a5a98;
  width: 200px;
  height: 44px;
  box-shadow: rgb(0 0 0 / 40%) 0px 2px 2px 0px, 
              rgb(0 0 0 / 24%) 0px 0px 1px 0px;
  border-radius: var(--border-radius);
}

.auth-form__socials--google{
  color: var(--text-color);
}

.auth-form__socials--google i{
  color: var(--primary-color);
}

.auth-form__socials-title{
  margin:0 14px;
  font-size: 1.4rem;
}



/* text-message css */

.auth-form__form-message{
  font-size: 1.6rem;
  color: var(--second-color);
  padding-top: 10px;
  line-height: 1.3;
  display: none;
}

.auth-form__form-message--active{
  display: block;
}

.form-container{
  position: relative;
}

/* loading */

.overlay__modal-content{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .05);
  display: -webkit-flex;
  display: flex;
}

.spinner-3 {
  margin: auto;
  width:50px;
  height:50px;
  border-radius:50%;
  background:conic-gradient(#0000 10%,#EE4D2D);
  -webkit-mask:radial-gradient(farthest-side,#0000 calc(100% - 8px),#000 0);
  -webkit-animation:s3 1s infinite linear;
          animation:s3 1s infinite linear;
}
@-webkit-keyframes s3 {to{-webkit-transform: rotate(1turn);transform: rotate(1turn)}}
@keyframes s3 {to{-webkit-transform: rotate(1turn);transform: rotate(1turn)}}


